.container {
	max-width: 100%;
	margin: auto;
  padding: 0 20px;
	@media (min-width: 320px) {
    max-width: 320px;
  }
  @media(min-width: 768px) {
    max-width: 768px;
  }

  @media (min-width: 1170px) {
    max-width: 1170px;
  }
	@media (min-width: 1320px) {
		max-width: 1320px;	
	}
}